.card{
&__body {
  padding: 0 10px 10px;
  border-radius: 8px;
  text-align: center;

  &:hover {
    box-shadow: 0 14px 26px -12px rgb(63, 102, 159);
  }
}
  &__header{
    height: 120px;
    display: flex;
    margin: 0 0 15px;
    justify-content: center;
    align-items: center;
  }
  &__title {
    width: 80%;
    text-align: center;
    color: rgb(202, 49, 30);
  }
  &__info {
    display: flex;
    justify-content: space-between;
    margin: 0 auto 15px;

  }
  &__star{
      height: 15px;
      margin: 5px;
    &:hover{
      cursor: pointer;
    }
  }
  &__pic{
    margin: 20px;
    display: block;
  }
  &__price{
    color: rgb(160, 20, 20);
    font-size: 25px;
    font-weight: bold;
  }
}
