.buttons {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%);
  display: flex;
  justify-content: space-around;
  width: 600px;

  &__btn  {
    background-color: #000000;
    border-radius: 3px;
    color: #ffffff;
    font-size: 15px;
    padding: 8px;
    text-decoration: none;
    border: none;
    outline: none;
    cursor: pointer;
  }
  &__btn_remove  {
    background-color: #000000;
    border-radius: 3px;
    color: #ffffff;
    font-size: 15px;
    padding: 8px;
    text-decoration: none;
    border: none;
    outline: none;
    cursor: pointer;
  }
}