.modal-success {
  width: 720px;
  position: fixed;
  top: 50vh;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #84ea92;
  z-index: 500;
  border: 1px solid green;
  border-radius: 5px;

  &__header {
    position: relative;
    background: #4bd662;
    display: flex;
    justify-content: space-between;


    &__title {
      color: #FFFFFF;
      padding:  30px;
      font-size: 25px;
      margin: 0;
    }

    & button {
      background: none;
      color: #FFFFFF;
      font-size: 40px;
    }
  }

  &__text {
    text-align: center;
    font-weight: 600;
    color: #FFFFFF;
    font-size: 20px;
  }

  &__footer {
    display: flex;
    justify-content: center;
    & button {
      margin: 25px 9px;
      border-radius: 5px;
    }
  }
}

.close-modal{
  width: 720px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #E74C3C;
  z-index: 50;

  &__header {
    display: flex;
    justify-content: space-between;
    background: #D44637;

    &__title {
      color: #FFFFFF;
      padding:30px;
      font-size: 22px;
      margin: 0;
    }

    & button {
      background: none;
      color: #FFFFFF;
      font-size: 35px;
    }
  }

  &__text {
    text-align: center;
    color: #FFFFFF;
    font-size: 17px;
    background: #E74C3C;
    padding: 15px 40px;
    font-weight: 500;
  }

  &__footer {
    display: flex;
    justify-content: center;
    & button {
      margin: 0 9px 24px;
      border-radius: 5px;
      padding: 10px 30px;
    }
  }
}

.shadow {
  position:fixed;
  display:flex;
  width:100%;
  height:100%;
  justify-content:center;
  align-items:center;
  background: rgba(0, 0, 0, 0.07);
  z-index:2;
  top: 0;
  left: 0;
}
